@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.cover-images {
    width: 320px;
    height: 200px;
    object-fit: cover;
    border-radius: 15px;
}
.type {
    font-size: 14px;
    color: #828282;
    display: flex;
    justify-content: center;
    align-items: center;
}
.super-host {
    color: #4f4f4f;
    border: 1px solid #4f4f4f;
    border-radius: 12px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 10px;
    padding: 6px;
}
.no-super-host {
    position: fixed;
    opacity: 0;
    pointer-events: none;
}
.rating {
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.title {
    max-width: 320px;
    font-size: 16px;
    font-weight: 600;
}
* {
  padding: 0;
  margin: 0;
}
html {
  padding-left: 8%;
  padding-right: 8%;
  background-color: #fafbfd;
}
div {
  font-family: 'Montserrat', sans-serif;
}
.header {
    position: fixed;
    width: 100%;
    height: 30vh;
    left: 0;
    top: -100%;
    z-index: 2;
    background-color: #fff;
    transition: 850ms;
}
.header.active {
    top: 0;
    transition: 350ms;
}
.black-background {
    position: fixed;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: -1;
    pointer-events: none;
    background-color: black;
    transition: 350ms;
    opacity: 0;
}
.black-background.hidden {
    pointer-events: all;
    opacity: 0.4;
    z-index: 1;
    transition: 350ms;
}
.semi {
    font-family: 'Mulish', sans-serif;
    font-size: 14px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: auto;
    padding: 10px 15px 10px 15px;
    transition: 0.4s;
    color: #bdbdbd;
}
.first-input {
    color: black;
}
.second-input {
    border-right: 1px solid #f2f2f2;
    border-left: 1px solid #f2f2f2;
}
.third-input {
    color: #EB5757;
}

